html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__link {
  color: #060b35;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__list {
  flex-wrap: nowrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav-hover, .nav__link:hover {
  color: #00acdc;
}

.img {
  width: 40px;
  height: 40px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

main {
  grid-template-columns: repeat(4, 200px);
  gap: 46px 48px;
  width: 944px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

.card {
  transition-duration: .3s;
  transition-duration: end .3s;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  justify-content: center;
  width: 198px;
  height: 406px;
  overflow: hidden;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 32px 19px 36px;
}

.card__title {
  color: #060b35;
  margin: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  font-family: Roboto, sans-serif;
  font-weight: 400px;
  color: #616070;
  margin-top: 4px;
  margin-left: 16px;
  font-size: 10px;
  line-height: 140%;
}

.card__reviews {
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.card__reviews-text {
  color: #060b35;
  text-align: right;
  margin-top: 0;
  margin-bottom: 26px;
  margin-right: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 140%;
}

.card__price {
  color: #616070;
  margin-left: 16px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 150%;
  display: flex;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  margin-top: 0;
  margin-left: 85px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 112%;
  display: flex;
}

.card__button-buy:hover {
  text-transform: uppercase;
  text-align: center;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 0 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: block;
}

button__button-buy {
  text-transform: uppercase;
  text-align: center;
  color: var(--white);
  background-color: #00acdc;
  border: 1px solid #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.stars {
  gap: 4px;
  margin-left: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__reviews-text {
  color: #060b35;
  text-align: right;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 140%;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-repeat: no-repeat;
}
